.filter__input-box {
  margin-top: 20px;
}
.filter__input[type="radio"],
.filter__input[type="checkbox"] {
  display: none;
}
.filter__input[type="radio"] + .filter__label {
  position: relative;
  padding-left: 34px;
}
.filter__input[type="radio"] + .filter__label::before {
  content: "";
  position: absolute;
  left: 0;
  width: 20px;
  height: 20px;
  background-color: #ffffff;
  border: 2px solid #bdbdbd;
  border-radius: 50%;
}
.filter__input[type="radio"] + .filter__label:hover::before {
  border-color: #9070ec;
}
.filter__input[type="radio"] + .filter__label:hover::after {
  content: "";
  position: absolute;
  top: 2px;
  left: 4px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: rgba(120, 89, 207, 0.25);
}
.filter__input[type="radio"]:checked + .filter__label::before {
  border-color: #7859cf;
}
.filter__input[type="radio"]:checked + .filter__label::after {
  content: "";
  position: absolute;
  top: 2px;
  left: 4px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #7859cf;
}
.filter__input[type="radio"]:disabled + .filter__label {
  color: #bdbdbd;
}
.filter__input[type="radio"]:disabled + .filter__label::before {
  border-color: #e2e2e2;
}
.filter__input[type="radio"]:disabled + .filter__label::after {
  background-color: #ffffff;
}
.filter__input[type="radio"]:checked:disabled + .filter__label::after {
  content: "";
  position: absolute;
  top: 2px;
  left: 4px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #e2e2e2;
}
.filter__input[type="checkbox"] + .filter__label {
  position: relative;
  padding-left: 52px;
}
.filter__input[type="checkbox"] + .filter__label::before {
  content: "";
  position: absolute;
  left: 0;
  box-sizing: border-box;
  width: 42px;
  height: 24px;
  background-color: #bdbdbd;
  border-radius: 12px;
}
.filter__input[type="checkbox"] + .filter__label::after {
  content: "";
  position: absolute;
  top: 0;
  left: 2px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #ffffff;
}
.filter__input[type="checkbox"] + .filter__label:hover::before {
  border: 2px solid #9070ec;
  background-color: rgba(120, 89, 207, 0.25);
}
.filter__input[type="checkbox"]:checked + .filter__label::before {
  background-color: #7859cf;
  border-color: #7859cf;
}
.filter__input[type="checkbox"]:checked + .filter__label::after {
  left: 20px;
}
.filter__input[type="checkbox"]:disabled + .filter__label {
  color: #bdbdbd;
}
.filter__input[type="checkbox"]:disabled + .filter__label::before {
  background-color: #e2e2e2;
  border-color: #e2e2e2;
}
.filter__input[type="checkbox"]:disabled + .filter__label::after {
  background-color: #f8f8f8;
}
.filter__input[type="checkbox"]:disabled:checked + .filter__label::after {
  left: 20px;
  background-color: #bdbdbd;
}
.filter__input[type="radio"] + .filter__label::before {
  top: 50%;
  transform: translateY(-50%);
}
.filter__input[type="checkbox"] + .filter__label::before {
  top: 50%;
  transform: translateY(-50%);
}
