.slider__scale {
  position: relative;
  height: 2px;
  background-color: #e2e2e2;
}
.slider__bar {
  position: absolute;
  top: -1px;
  height: 4px;
  background-color: #9070ec;
}
.slider__toggle {
  position: absolute;
  top: -11px;
  left: 0;
  width: 20px;
  height: 20px;
  background: #ffffff;
  border: 2px solid #9070ec;
  border-radius: 50%;
}
.slider__toggle--max {
  margin-left: -24px;
}
.slider__toggle:hover::after {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: rgba(120, 89, 207, 0.25);
}
.slider__toggle:active {
  border-color: #7859cf;
}
.slider__toggle:active::after {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #7859cf;
}
.slider:disabled .slider__bar,
.slider--disabled .slider__bar {
  background-color: #bdbdbd;
}
.slider:disabled .slider__toggle,
.slider--disabled .slider__toggle {
  border-color: #bdbdbd;
}
.slider:disabled .slider__toggle:hover::after,
.slider--disabled .slider__toggle:hover::after,
